'use strict';

// MODULES //

import setReadOnly from '@stdlib/utils-define-read-only-property';
import objectKeys from '@stdlib/utils-keys';


// MAIN //

/**
* Copies all enumerable own properties from a source object to a target object as enumerable read-only properties.
*
* @private
* @param {Object} target - target object
* @param {Object} source - source object
* @returns {Object} modified target object
*
* @example
* var source = {
*     'beep': 'boop'
* };
* var target = {};
*
* var out = assign( target, source );
* // returns <Object>
*
* var bool = ( out === target );
* // returns true
*
* var v = target.beep;
* // returns 'boop'
*/
function assign( target, source ) {
	var keys;
	var k;
	var i;

	keys = objectKeys( source );
	for ( i = 0; i < keys.length; i++ ) {
		k = keys[ i ];
		setReadOnly( target, k, source[ k ] );
	}
	return target;
}


// EXPORTS //

export default assign;
